/** @jsx jsx */
import { Link } from "gatsby"
import { jsx, useColorMode } from "theme-ui"
import replaceSlashes from "@lekoarts/gatsby-theme-minimal-blog/src/utils/replaceSlashes"
import useSiteMetadata from "@lekoarts/gatsby-theme-minimal-blog/src/hooks/use-site-metadata"
import useMinimalBlogConfig from "@lekoarts/gatsby-theme-minimal-blog/src/hooks/use-minimal-blog-config"
import { ReactComponent as Logo } from '../logo.svg';

const HeaderTitle = () => {
  const { siteTitle } = useSiteMetadata()
  const { basePath } = useMinimalBlogConfig()
  return (
    <Link
      to={replaceSlashes(`/${basePath}`)}
      aria-label={`${siteTitle} - 홈으로 돌아가기`}
      sx={{ color: `heading`, textDecoration: `none` }}
    >
      <div style={{ display: "flex", alignItems: "center" }} sx={{ my: 0, fontWeight: `medium`, fontSize: [3, 4] }}>
        <Logo width="35" height="35" className="logo" />
        <span style={{ marginLeft: "6px", fontSize: "1.2rem", fontWeight:"normal" }}>그린랩스</span>
        <span style={{ marginLeft: "6px", fontSize: "1.2rem", fontWeight:"bolder" }}>기술 블로그</span></div>
    </Link>
  )
}

export default HeaderTitle
